import React from 'react';
import Typography from '@material-ui/core/Typography';

import '../App.css';

export default function FAQ() {
  return(
    <div className="wrapperqanda">
      <Typography
        component="h1"
        variant="h3"
        align="center"
        gutterBottom
        className="qanda"
      >Questions and Answers</Typography>
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >It it confirmed that the wedding will take place this year?</Typography>
    <div>We would like to say yes 100%, but unfortunately, the last two years have proven that nothing is certain. We want our wedding to be exactly how we imagine it i.e no masks, surrounded by all of our people and to be able to dance all night. If these conditions are not possible, we will be postponing another year. However, we are very confident that this will be our year. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Where will the wedding take place?</Typography>
    <div>We are very lucky to be able to have you all join us at Melyssa's family cottage in Sutton. Weather permitting, the ceremony will take place outside and the reception will be under a tent. You will not have to change locations between the ceremony and reception, as they will both be meters away from each other. <br />Address: 200 ch du Havre, Sutton, QC</div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >I have mobility issues, will there be a lot of walking?</Typography>
    <div>There is going to be a little bit of walking on uneven ground. There will be ATVs available for people who need them.</div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Is there a dress code?</Typography>
    <div>Summery/festive cocktail attire - suits, pants and shirt, short dress, long dress, ... You do you. </div>
    <div>IMPORTANT - No heels!!! Everything will be in grass and we want you to be comfortable and enjoy the day. Don't do that to yourself. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Do you have a gift registry?</Typography>
    <div>We are extremely lucky to have love, laughter and a house full of furniture, so we do not have a registry. The most important thing to us is that you are able to celebrate with us on our wedding day. However, if you wish to give a gift, we will gratefully accept a contribution towards our honeymoon. There will be a box available for gifts on the wedding day. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >I want to be able to sleep nearby, are there any accomodations available?</Typography>
    <div>More details to come on this subject. We are working on a hotel deal and possibly a shuttle service. It is also a good idea to look into renting a cottage. It is also possible to pitch a tent or have a little camper on the property where the wedding will take place. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >When do I need to RSVP for?</Typography>
    <div>Please RSVP directly on the form linked on the website. Make sure you RSVP for all of the guests on your invitation. If you wanna invite a guest, please reach out to us. <br />RSVP by June 15th. </div>
    <br />
    <Typography
        component="h4"
        variant="h5"
        align="center"
        gutterBottom
        className="question"
      >Can I bring a date? </Typography>
    <div>Invites will be adressed to the people who are invited to the wedding. We unfortunately cannot allow everyone to bring plus ones. If you would like to bring someone spcial with you, we ask you to communicate with us as soon as possible. </div>
    
    
    </div>
  );
}