import React from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper} from '@material-ui/core';

import ScheduleOTD from '../images/schedulefr.jpg';

import '../App.css'
import Typography from '@material-ui/core/Typography';

function createData(name, calories) {
  return { name, calories};
}

const rows = [
  createData('15h-16h', 'Arrivée des invités'),
  createData('', "On demande à tous nos invités de prendre une belle photo avec nos photographes à leur arrivée. De cette façon, nous nous assurons d'avoir des photos de tous."),
  createData('16h', 'Cérémonie'),
  createData('', "Il y aura quelques places assises pour ceux qui en ont besoin, mais la majorité sera debout. Ce sera une cérémonie débranchée c.-à-d. aucune photo ou vidéo par les invités. Notre équipe s'en chargera. "),
  createData('16h30-18h', 'Cocktail'),
  createData('', "Il y a aura un peu de nourriture durant l'heure du cocktail, mais faites attention de ne pas ruiner votre souper!  "),
  createData('18h-3h', 'Réception'),
  createData('', "Souper Méchoui assis. C'est un évènement tout inclus. Ce sera une formule bar ouvert avec vin, bières et liqueurs ainsi que des options sans alcool. On veut que ce soit une soirée sans soucis pour tous. Toute notre équipe aura déjà reçu des pourboires. ")

];

export default function Schedule() {
  return(

    <div className="wrappersched">
      <Typography
        component="h1"
        variant="h3"
        align="center"
        gutterBottom
        className="qanda"
      >Horaire de la journée</Typography>
    <img style={{maxWidth: "100%"}} src={ScheduleOTD} alt="Horaire de la journée"></img>

    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name ? (<strong>{row.name}</strong>) : row.name}
              </TableCell>
              <TableCell style={{ maxWidth: '300px'}}align="left">{row.name ? (<strong>{row.calories}</strong>) : row.calories}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </div>
  );
}